@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

.Container {
    display: flex;
}

.mainContentContainer {
    min-height: 100vh;
    margin-left: 250px;
    width: calc(100% - 250px);
    background: rgb(218, 210, 210);
}

.sidebarHeader {
    padding: 10px;
    text-align: center
}

.error_message {
    color: #d9534f;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

/* Firefox */
input[type=number] {
    -moz-appearance: textfield;
}

.errorScreenContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%
}

.errorScreenContainer .errorMessage {
    color: #fff;
    font-weight: bold;
}
.sidebarContainer {
    position: fixed;
    height: 100vh;
    width: 250px;
    background: #11101d;
    color: #fff
}

.sidebarRootItem {
    padding: 15px;
    border-bottom: 1px solid black;
    cursor: pointer;
    transition: all 0.3s;
}

.sidebarRootItemActive {
    background: #1d1b31;

}


.sidebarRootItem:hover {
    color: cyan !important;
}

.sidebarRootItem:hover svg {
    fill: cyan !important;
}

.sidebarRootItem .listChild {
    transition: all 0.3s;
    max-height: 0px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    margin-top: 5px;
    /* background: #1d1b31; */
    color: #fff;
    /* align-items: center; */
}

.sidebarRootItem .listChildActive {
    display: block;
    max-height: 500px;
}

.sidebarRootItem .listChildDisable {
    max-height: 0
}

.sidebarItem {
    text-align: center;
    padding: 5px;
    cursor: pointer;
    opacity: 0.7;
}

.sidebarItem a {
    text-decoration: none !important;
    color: #fff
}

.sidebarItem a.active {
    color: cyan !important;

}


.sidebarItem:hover {
    color: cyan !important;
}

.sidebarItem:hover a {
    color: cyan !important;
}

.sidebarLabel {
    display: flex;
    align-items: center;
    /* justify-content:space-around */
}

.arrowIconContainer {
    flex-grow: 1;
    text-align: right;
    display: flex;
    flex-direction: row-reverse;
}

.arrowIcon {
    margin-top: 5px;
    /* flex-grow:1; */
}

.arrowIconActive {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}

.sidebarLabelContent {
    display: flex;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    /* max-width: 90%; */
    align-items: center;
    /* justify-content: space-evenly; */
    width: 80%;
}
.sidebarLabelContent > a{
    text-decoration: none !important;
    color:#fff;
}
.sidebarLabelContent:hover a{
    color:cyan !important;
}
.sidebarLabelContent > a.active{
    color:cyan !important;
}
*{
    margin: 0;
    padding: 0;
}

.loading{
    width: 80px;
    display: flex;
    flex-wrap: wrap;
    -webkit-animation: rotate 3s linear infinite;
            animation: rotate 3s linear infinite;  
}
.loading-screen {
    display: flex;
    justify-content: center;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1000;
    position: fixed;
    background: rgb(236, 227, 227);
    align-items: center;
}

@-webkit-keyframes rotate{
    to{
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes rotate{
    to{
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.loading span{
    width: 32px;
    height: 32px;
    background-color: red;
    margin: 4px;
}


.loading span:nth-child(1){
    border-radius: 50% 50% 0 50%;
    background-color: #ff5a5f;
    -webkit-transform-origin: bottom right;
            transform-origin: bottom right;
}

.loading span:nth-child(2){
    border-radius: 50% 50% 50% 0;
    background-color: #006c70;
    -webkit-transform-origin: bottom left;
            transform-origin: bottom left;
    -webkit-animation-delay: .5s;
            animation-delay: .5s;
}

.loading span:nth-child(3){
    border-radius: 50% 0 50% 50%;
    background-color: #f5a0a3;
    -webkit-transform-origin: top right;
            transform-origin: top right;
    -webkit-animation-delay: 1.5s;
            animation-delay: 1.5s;
}

.loading span:nth-child(4){
    border-radius: 0 50% 50% 50%;
    background-color: #474747;
    -webkit-transform-origin: top left;
            transform-origin: top left;
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
    
}
